"use client";

import { useEffect, useState } from "react";
import { jwtDecode } from "jwt-decode";
import PersonalDetails from "../components/PersonalDetails";
import EducationDetails from "../components/EducationDetails";

export default function Dashboard() {
  const [user, setUser] = useState(null);
  const [greeting, setGreeting] = useState("");

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) return;

    const decoded = jwtDecode(token);
    fetchUser(decoded.userId);

    const hour = new Date().getHours();
    if (hour < 12) {
      setGreeting("Good Morning");
    } else if (hour < 17) {
      setGreeting("Good Afternoon");
    } else {
      setGreeting("Good Evening");
    }
  }, []);

  const fetchUser = async (id) => {
    try {
      const res = await fetch(`/api/user/${id}`);
      const data = await res.json();
      if (res.ok) {
        setUser(data.user);
      }
    } catch (err) {
      console.error("Error fetching user", err);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-100 to-white p-6">
      <div className="max-w-5xl mx-auto bg-white shadow-lg rounded-2xl p-6 mb-10 flex items-center justify-between">
        <div>
          <h2 className="text-2xl sm:text-3xl font-semibold text-gray-700">{greeting},</h2>
          <h1 className="text-3xl sm:text-4xl font-bold text-blue-600 mt-1">
            {user?.name || "Welcome"}
          </h1>
          <p className="text-gray-500 text-sm mt-1">Hereâ€™s your personal dashboard overview</p>
        </div>
        <div className="shrink-0">
          <img
            src={user?.profileImage ? `/uploads/${user.profileImage}` : "/default-profile.png"}
            alt="Profile"
            className="w-20 h-20 sm:w-24 sm:h-24 rounded-full border-4 border-blue-200 shadow-md object-cover"
          />
        </div>
      </div>

      {/* Main Content */}
      {user ? (
        <div className="max-w-5xl mx-auto space-y-6">
          <PersonalDetails user={user} />
          {/* <EducationDetails user={user} /> */}
        </div>
      ) : (
        <div className="text-center text-gray-600">Loading your information...</div>
      )}
    </div>
  );
}
